import Dep from './dep';
import Watcher from './watcher';
class Vue {
  constructor(option) {
    this.$el = option.el;
    this.$data = this._data = option.data();

    this.init();
  }
  init() {
    this.observe(this.$data);
    this.compile(this.$el);
  }
  // ÂØπdataÂ±ûÊÄßËøõË°ågetÂíåsetÁõëÊéß
  observe(data) {
    if(typeof data !== 'object') {
      return void 0;
    }
    for(let k in data) {
      if(typeof data[k] === 'object') { // Â¶ÇÊûúdata[k]ËøòÊòØÂØπË±°ÁöÑËØùÔºåËøõË°åÈÄíÂΩíÊìç‰Ωú
        this.observe(data[k]);
      }else {
        let dep = new Dep();
        let val = data[k];
        Object.defineProperty(data, k, {
          get() {
            if(Dep.target) {
              dep.addSubscript(Dep.target);
            }
            return val;
          },
          set(newVal) {
            val = newVal;
            dep.notify();
          }
        });
      }
    }
  }
  // ÂØπhtmlËøõË°åËß£ÊûêÊìç‰ΩúÔºåÂ§ÑÁêÜ{{}}, v-xxxÁ≠âÊ†áËÆ∞
  compile(el) {
    let vm = this;
    let childNodes = Array.from(el.childNodes);
    let exp = /\{\{(.+)\}\}/g;
    childNodes.forEach(ele=> {
      // ÊñáÊú¨ËäÇÁÇπÂÜÖÂÆπÈáåÂê´Êúâ{{}}
      if(ele.nodeType===3&&exp.test(ele.textContent)) {
        // ËøôÈáåÈúÄË¶Ånew‰∏Ä‰∏™watcherÔºåÂπ∂ÊîæÁΩÆÂú®DepÁ±ªÁöÑtarget‰∏ä
        let expNameArr = RegExp.$1.split('.');
        let val = vm.$data;
        let watcher = new Watcher(()=> {
          let val = vm.$data;
          for(let k in expNameArr) {
            val = val[expNameArr[k]];
          }
          ele.textContent = val;
        })
        Dep.target = watcher;
        for(let k in expNameArr) {
          val = val[expNameArr[k]];
        }
        Dep.target = null;
        ele.textContent = ele.textContent.replace(/\{\{.+\}\}/g, val);
      }
      // ÂØπv-xxxÂ±ûÊÄßÂÅöÂ§ÑÁêÜ
      if(ele.nodeType===1) {
        let attributes = Array.from(ele.attributes);
        attributes.forEach(attr=> {
          if(attr.name.indexOf('v-')===0) {
            let director = attr.name.slice(2);
            let epx = attr.value;
            
            console.log(director);
          }
        });      
      }
      
      if(ele.nodeType===1&&ele.childNodes.length>0) {
        this.compile(ele);
      }
    });
  }
}

export default Vue;